@use 'base/mixins' as m;
@use 'base/variables' as v;

.header{
    min-height: 60rem; //que minimo mida 60
    background-image: url(../img/grafico.svg),url(../img/grafico.svg),linear-gradient(to right, rgba(v.$negro,1),rgba(v.$negro,.5)), image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x,url(../img/header.jpg) 1x );

    background-repeat: no-repeat;
    background-size: 400px,400px,cover,cover; //El primero aplica al grafico el segundo al grafiso, el tercero al gradient, el cuarto al header
    background-position: top left,bottom right,center center, center center;
 &__contenedor{
 @include m.contenedor;
 }
 &__navegacion{
    padding: 5rem 0;
    display: flex;
    justify-content: center;
    @include m.tablet{
        justify-content: flex-end;
    }
    gap: 1.5rem;
 }
 &__enlace{
    color: v.$blanco;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: v.$bold;

    &:hover{
        color: v.$azul;
    }
 }
 &__contenido{
    padding-top: 10rem;

    @include m.tablet{
        padding-top: 5rem;
    }
 }
 &__logo{
    @include m.texto-fluido(2rem, 8rem);
    @include m.texto-degradado(v.$azul,v.$secundario);
    text-align: center;
    @include m.tablet{
        text-align: right;
        display: inline-block;
    }
 }

 &__texto{
    font-size: 3rem;
    font-weight: v.$bold;
    color: v.$blanco;
    margin: 2rem 0;
    text-align: center;
    text-transform: uppercase;
    @include m.tablet{
        text-align: left;
    }
    &--modalidad{
        font-size: 2rem;
    }
 }

 &__boton{
    @include m.boton(v.$secundario,v.$blanco);
    text-transform: uppercase;
    margin: 2rem 0;
    @include m.tablet{
        width: auto;
    }
 }
}